    "Barriocanal Feltes, Raúl Fernando",
    "Birbaumer Vera, Georg Daniel",
    "Black Zaldívar, Weldon Walter",
    "Boggino Ramallo, Juan Carlos",
    "Breuer González, Luís Alberto",
    "Callizo, María del Pilar",
    "Caniza Pecci, José María",
    "Capurro de Sacco, Celia Helena",
